import React, { PropsWithChildren, useState } from 'react';
import Topbar from 'components/Topbar';
import { makeStyles, Modal } from '@material-ui/core';
import MenuModal from 'components/MenuModal';

function Layout(props: PropsWithChildren) {
  const { children } = props;
  const classes = useStyles();
  const [isDisplayMenuModal, setIsDisplayModal] = useState<boolean>(false);

  const setDislay = () => {
    setIsDisplayModal(!isDisplayMenuModal);
  };

  return (
    <div className={classes.container}>
      <Topbar displayModal={setDislay} />
      {children}
      <Modal open={isDisplayMenuModal}>
        <MenuModal onClose={() => setIsDisplayModal(false)} />
      </Modal>

    </div>
  );
}

export default Layout;

const useStyles = makeStyles(() => ({
  container: {
    zIndex: 1000,
    minHeight: '100vh',
    width: '100%',
    padding: '20px 20px',
    background: "#0f0933"
  },
  video: {
    zIndex: -10,
    position: "fixed",
    right: 0,
    bottom: 0,
    minWidth: "100%",
    minHeight: "100%"
  }
}));
