import React, { useCallback, useState } from 'react';
import { Button, Typography } from '@material-ui/core';
import { useStyles } from './styles';
import CommonDialog from 'components/common/CommonDialog';
import Web3 from 'web3';
import { abi } from 'services/contract';
import ReactLoading from 'react-loading';

export interface EndRoundProps {
  getProvider: () => any;
  getSomeFiContractAddress: () => string | undefined;
  account?: string | null;
  setOpenSnack: (value: boolean) => void;
  setSnackState: (vlaue: { type: any; content: string }) => void;
}

const EndRound = (props: EndRoundProps) => {
  const {
    account,
    getProvider,
    getSomeFiContractAddress,
    setOpenSnack,
    setSnackState,
  } = props;
  const styles = useStyles();
  const [openConfirm, setOpenConfirm] = useState(false);
  const [loading, setLoading] = useState<boolean>(false);

  const openConfirmDialog = useCallback(() => {
    setOpenConfirm(true);
  }, [setOpenConfirm]);
  // For local testing, don't remove
  // const initialize = useCallback(async () => {
  //   const someFiContractAddress = getSomeFiContractAddress();
  //   const web3 = new Web3(getProvider());
  //   const someFiContract = new web3.eth.Contract(
  //     abi as any,
  //     someFiContractAddress,
  //   );
  //   await someFiContract.methods.initialize('0x429b99aa15D2174dD3fB80c6a0e0db5fb7202631','0xF1EBf8D4594c96Ae9E2C503B083c81c315c6F3dA','0xbf969529EcB5115cc9929c0f4bF8e0c493bF60B1','0x28985954bE5e830F548287ff94520F3cffcdB8De').send({ from: account });
  // }, [getSomeFiContractAddress, getProvider, account]);

  const closeConfirmDialog = useCallback(() => {
    setOpenConfirm(false);
  }, [setOpenConfirm]);

  const handleConfirm = useCallback(async () => {
    const someFiContractAddress = getSomeFiContractAddress();
    const web3 = new Web3(getProvider());
    const someFiContract = new web3.eth.Contract(
      abi as any,
      someFiContractAddress,
    );
    try {
      setLoading(true);
      await someFiContract.methods.closeIco().send({ from: account });
      setOpenConfirm(false);
      setLoading(false);
      setSnackState({
        type: 'success',
        content: 'the round has ended',
      });
      setOpenConfirm(false);
      setOpenSnack(true);
    } catch (error) {
      setLoading(false);
      setSnackState({
        type: 'error',
        content: 'something was wrong',
      });
      setOpenConfirm(false);
      setOpenSnack(true);
    }
  }, [
    account,
    getProvider,
    getSomeFiContractAddress,
    setOpenConfirm,
    setOpenSnack,
    setSnackState,
  ]);

  return (
    <div className={styles.container}>
      <div className={styles.buttonConatiner}>
        <Button className={styles.button} onClick={openConfirmDialog}>
          End Round
        </Button>
      </div>
      <CommonDialog open={openConfirm} setOpen={openConfirmDialog}>
        <div className={styles.containerDialog}>
          <Typography className={styles.title}>
            Are you sure do you want to end round
          </Typography>
          <div className={styles.buttonsContainer}>
            <Button
              className={styles.confirmButton}
              onClick={handleConfirm}
              disabled={loading}
            >
              {loading ? (
                <ReactLoading
                  type="spinningBubbles"
                  color="#FFFFFF"
                  width={20}
                  height={'100%'}
                />
              ) : (
                'Confirm'
              )}
            </Button>

            <Button
              className={styles.cancelButton}
              onClick={closeConfirmDialog}
              disabled={loading}
            >
              Cancel
            </Button>
          </div>
        </div>
      </CommonDialog>
    </div>
  );
};

export default EndRound;
