import React, {PropsWithChildren, useCallback, useEffect, useState} from 'react';
import { Box, Button, Grid, makeStyles } from '@material-ui/core';
import { formatEther } from '@ethersproject/units';
import { renderShortAddress } from 'helpers';
import Web3 from "web3";
import {erc20abi, abi} from "../../services/contract";
import {useWeb3React} from "@web3-react/core";
import theme from "../../material";

interface UerInfoProps {
  totalPackageSize: string | number;
  amount?: string | number;
  currentPackageSize: string | number;
  profitClaimed: string | number;
}

function UserInfo(props: PropsWithChildren<UerInfoProps>) {
  const { children, amount, currentPackageSize, totalPackageSize, profitClaimed } =
    props;
  const classes = useStyles();

  const [balance, setBalance] = useState('0');


  const { library, account, active } = useWeb3React();
  const getProvider = useCallback(() => {
    return library?.provider || window.web3?.currentProvider;
  }, [library?.provider]);

  const getWalletAddressEcr20 = () => {
    return process.env.REACT_APP_BSC_USDT_ADDRESS;
  };

  const getWalletAddress = () => {
    return process.env.REACT_APP_BSC_CONTRACT_ADDRESS;
  }

  useEffect(() => {
    if (!account || !library) return;

    const web3 = new Web3(getProvider());
    const contractEcr20 = new web3.eth.Contract( erc20abi as any, getWalletAddressEcr20(), );

    contractEcr20.methods
        .balanceOf(account)
        .call()
        .then((res: any) => {
          setBalance(res || '0');
        });

  }, [library, account, getProvider]);

  const [accountInfo, setAccountInfo] = useState({
    currentPackageSize: 0
  });

  const roundInfoFecth = useCallback(async () => {
    const provider = getProvider();
    if (!provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(abi as any, getWalletAddress());
    try {
      const roundId = await contract.methods.roundId().call();
      const res = await contract.methods.refInfo(account, roundId).call()
      setAccountInfo({
        currentPackageSize: res.packageAmount
      })

    } catch (error) {}
  }, [getProvider]);

  useEffect(() => {
    roundInfoFecth();
  }, [roundInfoFecth]);

  const handleClaim = async () => {
    const web3 = new Web3(getProvider());
    const lottery = new web3.eth.Contract(abi as any, getWalletAddress());
    try {
      // setClaimLoading(true);
      await lottery.methods.userWithdraw().send({
        from: account,
      });
      // const refInfo = await lottery.methods.refInfo(account, roundId).call();
      // const { profitClaimed } = refInfo;
      // setSnackState({
      //   type: 'success',
      //   content: 'Claimed successfully',
      // });
      // // setNodeInfo((prev) => ({ ...prev, profitClaimed: Number(profitClaimed) / 10 ** 18 }));
      // setCommission(0);
      // setOpenSnack(true);
      // setClaimLoading(false);
    } catch (error) {
      console.log('error claim: ', error);
      // setSnackState({
      //   type: 'error',
      //   content: 'Claimed fail',
      // });
      // setOpenSnack(true);
      // setClaimLoading(false);
    }
  };

  return (
      <Grid className={classes.container}  spacing={2}>
        <div className={classes.package}>
          <div className={classes.title}>
            <p style={{fontWeight: 'bold', fontSize:"22px", margin: 0}}>My Package</p>
            <p style={{fontWeight: 'bold', fontSize:"30px", margin: 0}}>5000 USDT</p>
          </div>
        </div>
        <div className={classes.profit}>
          <div className={classes.title}>
            <p style={{fontWeight: 'bold', fontSize:"22px", margin: 0}}>Total Profit</p>
            <p style={{fontWeight: 'bold', fontSize:"30px", margin: 0}}>300 USDT</p>
          </div>
          <Button
              onClick={() => {handleClaim()}}
              className={classes.button}
          >
            Claim
          </Button>
        </div>
      </Grid>
  );
}

export default UserInfo;

