import React, {
  PropsWithChildren,
  useCallback,
  useEffect,
  useState,
} from 'react';
import { Grid, Button } from '@material-ui/core';
import moment from 'moment';
import CommonInput from 'components/common/CommonInput';
import TimePicker from 'components/common/TimePicker';
import { useStyles } from './styles';
import isNil from 'lodash/isNil';
import { isStringNumber } from 'helpers';
import { AMOUNT_TOKEN_PER_ROUND, PRICE_PER_TOKEN } from 'common/constant';
import Web3 from 'web3';
import { abi } from 'services/contract';
import ReactLoading from 'react-loading';

export interface CreateRoundProps {
  getProvider: () => any;
  getSomeFiContractAddress: () => string | undefined;
  account?: string | null;
  setOpenSnack: (value: boolean) => void;
  setSnackState: (vlaue: { type: any; content: string }) => void;
}

const CreateRound = (props: PropsWithChildren<CreateRoundProps>) => {
  const {
    account,
    getProvider,
    getSomeFiContractAddress,
    setOpenSnack,
    setSnackState,
    children,
  } = props;
  const styles = useStyles();
  const [amount, setAmount] = useState(AMOUNT_TOKEN_PER_ROUND);
  const [pricePerToken, setPricePerToken] = useState(PRICE_PER_TOKEN);
  const [startTime, setStartTime] = useState<Date | null>(
    moment().add(3, 'minute').toDate(),
  );
  const [loading, setLoading] = useState<boolean>(false);

  const handleChangeAmount = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const value = event.target.value;
      if (isNil(value) || value === '') {
        setAmount(value);
      } else {
        if (!isStringNumber(value)) {
          setAmount(isNil(amount) ? '' : amount);
          return;
        }
        setAmount(value.replace(',', '.'));
      }
    },
    [amount, setAmount],
  );

  const setPkgRate = useCallback(async () => {
    const someFiContractAddress = getSomeFiContractAddress();
    const web3 = new Web3(getProvider());
    const someFiContract = new web3.eth.Contract(
      abi as any,
      someFiContractAddress,
    );
    await someFiContract.methods
      .setPkgRate([100, 500, 1000, 2000, 5000], [500, 700, 800, 900, 1000])
      .send({ from: account });
  }, [getSomeFiContractAddress, getProvider, account]);

  const handleChanePricePerToken = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const value = event.target.value;
      if (isNil(value) || value === '') {
        setPricePerToken(value);
      } else {
        if (!isStringNumber(value)) {
          setPricePerToken(isNil(pricePerToken) ? '' : pricePerToken);
          return;
        }
        setPricePerToken(value.replace(',', '.'));
      }
    },
    [pricePerToken, setPricePerToken],
  );

  const handleChangeStartTime = useCallback(
    (time: Date | null) => {
      setStartTime(time);
    },
    [setStartTime],
  );

  useEffect(() => {});

  const handleCreateRound = useCallback(async () => {
    const timesStamp = moment(startTime).unix();
    console.log(timesStamp, 'timesStamp');
    const someFiContractAddress = getSomeFiContractAddress();
    const web3 = new Web3(getProvider());
    const someFiContract = new web3.eth.Contract(
      abi as any,
      someFiContractAddress,
    );
    try {
      setLoading(true);
      await someFiContract.methods
        .setRoundInfo(timesStamp, Number(amount), Number(pricePerToken))
        .send({ from: account });
      setLoading(false);
      setSnackState({
        type: 'success',
        content: 'the round has created',
      });
      setOpenSnack(true);
    } catch (error) {
      setLoading(false);
      setSnackState({
        type: 'error',
        content: 'something was wrong',
      });
      setOpenSnack(true);
    }
  }, [
    startTime,
    amount,
    pricePerToken,
    account,
    getProvider,
    getSomeFiContractAddress,
    setOpenSnack,
    setSnackState,
  ]);

  return (
    <>
      <div className={styles.container}>
        <Grid container className={styles.form} spacing={2}>
          <Grid container item className={styles.titleContainer}>
            <h2 className={styles.title}>Create Round Sale</h2>
          </Grid>
          <Grid container item>
            <Grid item xs={4} className={styles.label}>
              Start time
            </Grid>
            <Grid item xs={8}>
              <TimePicker value={startTime} onChange={handleChangeStartTime} />
            </Grid>
          </Grid>
          <Grid container item>
            <Grid item xs={4} className={styles.label}>
              Amount
            </Grid>
            <Grid item xs={8}>
              <CommonInput
                placeholder="Amount"
                value={amount}
                onChange={handleChangeAmount}
                className={styles.amount}
              />
            </Grid>
          </Grid>
          <Grid container item>
            <Grid item xs={4} className={styles.label}>
              USDT per SOFI
            </Grid>
            <Grid item xs={8}>
              <CommonInput
                placeholder="1 USDT = ? SOFI"
                value={pricePerToken}
                onChange={handleChanePricePerToken}
                className={styles.amount}
              />
            </Grid>
          </Grid>
          <Grid container item className={styles.buttonContainer} spacing={2}>
            <Grid item>
              <Button
                className={styles.button}
                onClick={handleCreateRound}
                disabled={loading}
              >
                {loading ? (
                  <ReactLoading
                    type="spinningBubbles"
                    color="#FFFFFF"
                    width={20}
                    height={'100%'}
                  />
                ) : (
                  'Create'
                )}
              </Button>
            </Grid>
            <Grid item>
              <Button className={styles.button} onClick={setPkgRate}>
                setPkgRate
              </Button>
            </Grid>
          </Grid>
        </Grid>
        {children}
      </div>
    </>
  );
};

export default CreateRound;
