import React, { useEffect, useMemo, useCallback, useState } from 'react';
import { useWeb3React } from '@web3-react/core';
import { LocalStorageEnum } from 'enums/storage';
import localStorage from 'utils/localStorage';
import { injected, walletconnect } from 'services/wallet';
import CustomTabs from 'components/common/CustomTabs';
import CreateRound from 'components/Admin/CreateRound';
import EndRound from 'components/Admin/EndRound';
import Report from 'components/Admin/Report';
import { useStyles } from './styles';
import CommonSnackBar from 'components/common/CommonSnackBar';
import ListMember from 'components/Admin/Listmember';

const Admin = (props: any) => {
  const styles = useStyles();
  const { library, account, activate } = useWeb3React();

  const [openSnack, setOpenSnack] = useState(false);
  const [snackState, setSnackState] = useState<{ type: any; content: string }>({
    type: 'error',
    content: '',
  });

  const getProvider = useCallback(() => {
    return library?.provider || window.web3?.currentProvider;
  }, [library]);

  const getSomeFiContractAddress = useCallback(() => {
    return process.env.REACT_APP_BSC_CONTRACT_ADDRESS;
  }, []);

  useEffect(() => {
    const connector = localStorage.getItem(LocalStorageEnum.CONNECTOR);
    if (connector) {
      if (connector === 'injected') activate(injected);
      else if (connector === 'walletconnect') activate(walletconnect);
    }
  }, [activate]);

  const tabList = useMemo(
    () => [
      {
        label: 'Report',
        component: (
          <Report
            account={account}
            getProvider={getProvider}
            getSomeFiContractAddress={getSomeFiContractAddress}
          />
        ),
      },
      {
        label: 'List member',
        component: (
          <ListMember
            account={account}
            getProvider={getProvider}
            getSomeFiContractAddress={getSomeFiContractAddress}
          />
        ),
      },
      {
        label: 'Round Setup',
        component: (
          <CreateRound
            account={account}
            getProvider={getProvider}
            getSomeFiContractAddress={getSomeFiContractAddress}
            setOpenSnack={setOpenSnack}
            setSnackState={setSnackState}
          >
            <EndRound
              account={account}
              getProvider={getProvider}
              getSomeFiContractAddress={getSomeFiContractAddress}
              setOpenSnack={setOpenSnack}
              setSnackState={setSnackState}
            />
          </CreateRound>
        ),
      },
    ],
    [account, getProvider, getSomeFiContractAddress],
  );

  return (
    <div className={styles.container}>
      <CustomTabs tabList={tabList} />
      <CommonSnackBar
        openSnack={openSnack}
        setOpenSnack={setOpenSnack}
        type={snackState.type}
        content={snackState.content}
      />
    </div>
  );
};

export default Admin;
