import React from 'react';
import { Box, makeStyles, useMediaQuery, useTheme } from '@material-ui/core';
import MenuIcon from 'icons/MenuIcon';
import clsx from 'clsx';
import { useWeb3React } from '@web3-react/core';
import { useHistory } from 'react-router-dom';

interface ITopbar {
  displayModal: () => void;
}

function Topbar(props: ITopbar) {
  const { displayModal } = props;
  const classes = useStyles();
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('lg'));
  const isMb = useMediaQuery(theme.breakpoints.down('sm'));

  const { account } = useWeb3React();
  const history = useHistory();
  const directToDashboard = () => {
    history.push('/');
  };

  return (
    <Box
      display={'flex'}
      alignItems={'center'}
      className={classes.container}
      justifyContent={'space-between'}
    >
      <div onClick={directToDashboard} style={{ cursor: 'pointer' }}>
        <a href="https://somefi.finance">
          <img alt="somefi logo" src="/images/logo.png" className={classes.img} /></a>
      </div>
      {isDesktop ? (
        <ul className={classes.list}>
          <li onClick={directToDashboard}>DASHBOARD</li>
          {account && (
            <li onClick={() => history.push(`/profile?wallet=${account}`)}>
              PROFILE
            </li>
          )}
          {account && <li>LOGOUT</li>}
        </ul>
      ) : (
        <Box className={clsx(classes.icon)} onClick={displayModal}>
          <MenuIcon
            color="white"
            width={isMb ? 20 : 24}
            height={isMb ? 20 : 24}
          />
        </Box>
      )}
    </Box>
  );
}

export default Topbar;

const useStyles = makeStyles((theme) => ({
  container: {
    padding: '0 120px',
    [theme.breakpoints.down('md')]: {
      padding: '0 10px',
    },
  },
  icon: {
    width: 60,
    height: 60,
    [theme.breakpoints.down('sm')]: {
      width: 50,
      height: 50,
    },
    background: 'linear-gradient(251deg, rgb(139, 51, 124), rgb(199, 43, 97))',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  img: {
    width: 220,
    height: 71,
    objectFit: 'cover',
  },
  list: {
    float: 'none',
    display: 'flex',
    width: '100%',
    'list-style-type': 'none',
    justifyContent: 'flex-end',
    '&>li': {
      marginLeft: 40,
      fontSize: '1.3em',
      fonrWeight: 600,
      cursor: 'pointer',
    },
  },
}));
