/* eslint-disable react/no-array-index-key */
import React, { useCallback, useMemo, useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Tab from '@material-ui/core/Tab';
import TabContext from '@material-ui/lab/TabContext';
import TabList from '@material-ui/lab/TabList';
import TabPanel from '@material-ui/lab/TabPanel';
import clsx from 'clsx';
import Paper from '@material-ui/core/Paper';
import { useStyles } from './styles';
import { Box, Modal, useMediaQuery, useTheme } from '@material-ui/core';
import MenuIcon from 'icons/MenuIcon';
import OptionModal from 'containers/Admin/OptionModal';

export interface MuiTab {
  label: string;
  component: React.ReactNode;
}

export interface CustomTabsProps {
  tabList: Array<MuiTab>;
  defaultTabKey?: string;
}

function CustomTabs(props: CustomTabsProps) {
  const classes = useStyles(props);

  const theme = useTheme();

  const isMb = useMediaQuery(theme.breakpoints.down('sm'));

  const [modal, setModal] = useState<boolean>(false);

  const { tabList, defaultTabKey = '0' } = props;
  const [currentTab, setCurrentTab] = React.useState(defaultTabKey);

  //@tsingore
  const onChangeTab = useCallback(
    (event: React.ChangeEvent<{}>, newValue: string) => {
      setCurrentTab(newValue);
    },
    [],
  );

  const TabHeader = useMemo(
    () => (
      <TabList
        onChange={onChangeTab}
        aria-label="mui-custom-tab"
        classes={{
          indicator: classes.indicator,
          scroller: classes.scroller,
          flexContainer: classes.flexContainer,
        }}
      >
        {tabList.map((tab, index) => (
          <Tab
            disableRipple
            key={index}
            label={tab.label}
            value={`${index}`}
            className={clsx(
              classes.muiTab,
              `tab-${index + 1}`,
              `${index}` === currentTab && classes.isActive,
            )}
          />
        ))}
      </TabList>
    ),
    [tabList, classes, currentTab, onChangeTab],
  );

  const TabBody = useMemo(
    () =>
      tabList.map((tab, index) => (
        <TabPanel value={`${index}`} key={index} className={classes.body}>
          {tab.component}
        </TabPanel>
      )),
    [tabList, classes],
  );

  if (!tabList.length) return null;

  return (
    <Paper className={classes.root} elevation={0}>
      <TabContext value={currentTab}>
        {!isMb ? (
          <AppBar position="static" className={classes.appBar}>
            {TabHeader}
          </AppBar>
        ) : (
          <Box
            className={clsx(classes.icon)}
            onClick={() => {
              setModal(!modal);
            }}
          >
            <MenuIcon
              color="white"
              width={isMb ? 20 : 24}
              height={isMb ? 20 : 24}
            />
          </Box>
        )}
        {TabBody}
      </TabContext>
      <Modal open={modal}>
        <OptionModal
          onClose={() => {
            setModal(false);
          }}
          onChangeTap={(tap: string) => setCurrentTab(tap)}
        />
      </Modal>
    </Paper>
  );
}

export default CustomTabs;
