import { Box, Button, makeStyles, Typography } from '@material-ui/core';
import CommonDialog from 'components/common/CommonDialog';
import CommonInput from 'components/common/CommonInput';
import { LocalStorageEnum } from 'enums/storage';
import React, { useCallback, useState } from 'react';

interface IProps {
  openConfirm: boolean;
  setOpenConfirm: (value: boolean) => void;
  setSnackState: (value: any) => void;
  setOpenSnack: (value: boolean) => void;
}

const ConfirmReferral = ({
  openConfirm,
  setOpenConfirm,
  setOpenSnack,
  setSnackState,
}: IProps) => {
  const classes = useStyles();
  const [value, setValue] = useState('');

  const handleChangeValue = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      const v = event.target.value;
      setValue(v);
    },
    [],
  );

  const onConfirm = () => {
    localStorage.setItem(LocalStorageEnum.REFERRAL, value);
    setOpenConfirm(false);
    setSnackState({
      type: 'success',
      content: 'Successfully Joined',
    });
    setOpenSnack(true);
  };

  return (
    <CommonDialog open={openConfirm} setOpen={setOpenConfirm}>
      <Box className={classes.container}>
        <Box className={classes.header}>
          <Typography>Join System</Typography>
        </Box>
        <Box width="100%">
          <Typography>Referral</Typography>
          <CommonInput
            value={value}
            onChange={handleChangeValue}
            className={classes.input}
          />
        </Box>
        <Button className={classes.btn} onClick={onConfirm}>
          Confirm
        </Button>
        <Button className={classes.btn} onClick={onConfirm}>
          Later
        </Button>
        <Typography className={classes.footer}>
          Fill in the referral address in the box to participate!
        </Typography>
      </Box>
    </CommonDialog>
  );
};

export default ConfirmReferral;

const useStyles = makeStyles((theme) => ({
  container: {
    backgroundColor: 'rgb(42, 41, 53)',
    padding: 16,
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
    alignItems: 'center',
  },
  header: {
    width: '100%',
    textAlign: 'center',
    '& p': {
      fontSize: '2rem',
      marginTop: 16,
      marginBottom: 16,
      fontWeight: 600,
    },
  },
  input: {
    width: '100%',
    border: '1px solid hsla(0,0%,100%,.2)',
    borderRadius: 10,
    marginTop: 16,
    '& input': {
      height: 48,
      fontSize: '1.25rem',
      marginLeft: 8,
    },
  },
  footer: {
    marginTop: 24,
    width: 'fit-content',
  },
  btn: {
    marginTop: 16,
    width: '100%',
    height: 48,
    background: 'linear-gradient(251deg, rgb(139, 51, 124), rgb(199, 43, 97))',
    borderRadius: 10,
    textTransform: 'none',
    fontSize: '1.375rem',
    fontWeight: 500,
  },
}));
