import React, { useCallback, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core';
import TableList from './Table';
import Web3 from 'web3';
import { abi } from 'services/contract';

interface ListMemberProps {
  getProvider: () => any;
  getSomeFiContractAddress: () => string | undefined;
  account?: string | null;
}

function ListMember(props: ListMemberProps) {
  const { getProvider } = props;
  const classes = useStyles();
  const [search, setSearch] = useState<string>('');
  const [ListMember, setListMember] = useState<Array<any>>([]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setSearch(e.target.value);
    if (!e.target.value) fetchListMember();
  };

  const handleSearch = () => {
    const result = ListMember.find((member) => member?.address === search);
    setListMember([result]);
  };

  const fetchListMember = useCallback(async () => {
    const provider = getProvider();
    if (!provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(
      abi as any,
      process.env.REACT_APP_BSC_CONTRACT_ADDRESS,
    );

    try {
      const roundId = await contract.methods.roundId().call();
      const response = (await contract.methods
        .getListUserByRoundId(roundId)
        .call()) as any[];

      response.map(async (address) => {
        const refInfo = await contract.methods.refInfo(address, roundId).call();
        const res = { ...refInfo, address };
        setListMember((prev) => [...prev, res]);
      });
    } catch (error) {
      console.log('catch: ', error);
    }
  }, [getProvider]);

  useEffect(() => {
    fetchListMember();
  }, [fetchListMember]);

  return (
    <div className={classes.container}>
      <input
        onChange={handleChange}
        value={search}
        className={classes.input}
        placeholder="Enter wallet adrress to search..."
      />
      <button className={classes.button} onClick={handleSearch}>
        Search
      </button>
      <TableList
        rows={ListMember}
        columns={[
          'No.',
          'Wallet adress',
          'Investment',
          'Branch Investment',
          'Profit',
        ]}
      />
    </div>
  );
}

export default ListMember;

const useStyles = makeStyles((theme) => ({
  container: {
    marginTop: 100,
    [theme.breakpoints.down('sm')]: {
      marginTop: 25,
    },
  },
  input: {
    marginBottom: 20,
    height: 30,
    width: 200,
    borderRadius: 6,
    outline: 'none',
    border: 'none',
  },

  button: {
    background: 'linear-gradient(251deg, rgb(139, 51, 124), rgb(199, 43, 97))',
    border: '1px solid rgb(58, 36, 133)',
    borderRadius: 8,
    color: 'white',
    fontSize: 14,
    fontWeight: 600,
    padding: 5,
    cursor: 'pointer',
    marginLeft: 10,
  },
}));
