import { Dialog, makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import React, { ReactElement } from 'react';

interface IDialog {
  children: ReactElement;
  open: boolean;
  setOpen: (value: boolean) => void;
}
const CommonDialog = ({ children, open, setOpen }: IDialog) => {
  const classes = useStyles();

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Dialog open={open} onClose={handleClose} className={clsx(classes.main)}>
      {children}
    </Dialog>
  );
};

export default CommonDialog;

const useStyles = makeStyles((theme) => ({
  main: {
    '& .MuiPaper-root': {
      maxWidth: 600,
      width: '100%',
      boxShadow: 'none',
      backgroundColor: 'transparent',
      paddingLeft: 12,
      paddingRight: 12,
      [theme.breakpoints.down('sm')]: {
        paddingLeft: 8,
        paddingRight: 8,
      },
    },
  },
}));
