import React, { useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
import BuyToken from '../BuyToken';
import Admin from '../Admin';
import { RoutesEnum } from 'enums/routes';
import Layout from 'layout';
// import Profile from 'containers/Profile';
import { LocalStorageEnum } from 'enums/storage';
import { injected, walletconnect } from 'services/wallet';
import { useWeb3React } from '@web3-react/core';

function AppRoutes() {
  const { activate } = useWeb3React();

  useEffect(() => {
    const connector = localStorage.getItem(LocalStorageEnum.CONNECTOR);
    if (connector) {
      if (connector === 'injected') activate(injected);
      else if (connector === 'walletconnect') activate(walletconnect);
    }
  }, [activate]);

  return (
    <Switch>
      <Route
        path={RoutesEnum.Home}
        exact
        render={() => (
          <Layout>
            <BuyToken />
          </Layout>
        )}
      />
      <Route path={RoutesEnum.Admin} component={Admin} exact />
    </Switch>
  );
}

export default AppRoutes;
