import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { Box, Button, makeStyles, Typography, Grid } from '@material-ui/core';
import { useWeb3React } from '@web3-react/core';
import clsx from 'clsx';
import AmountAndBalance from 'components/ConnectWallet/AmountAndBalance';
import TimeLine, { TIMELINE_ID } from 'components/TimeLine';
import ArrowDownIcon from 'icons/ArrowDownIcon';
import { ChainID } from 'types';
import ConnectWallet from '../../components/ConnectWallet';
import Web3 from 'web3';
import { abi, erc20abi } from 'services/contract';
import CommonSnackBar from 'components/common/CommonSnackBar';
import { formatEther } from '@ethersproject/units';
import ConfirmReferral from 'components/ConfirmReferral';
import localStorage from 'utils/localStorage';
import { LocalStorageEnum } from 'enums/storage';
import ReactLoading from 'react-loading';
import Claim from 'components/Claim';
import NodeInfo from 'components/NodeInfo';
import Package from 'components/Package';
import { NONE_ADDRESS } from 'enums/const';
import { TimeLineProp } from 'components/TimeLine';
import axios from 'axios';
import theme from "../../material";

const BuyToken = () => {
  const classes = useStyles();
  const [balance, setBalance] = useState('0');
  const [amount, setAmount] = useState<string>('100');
  const [isApprove, setIsApprove] = useState(true);
  const [isExpired, setIsExpired] = useState(false);
  const [isProgressApprove, setProgressApprove] = useState(false);
  const [openLoading, setOpenLoading] = useState(false);
  const [openSnack, setOpenSnack] = useState(false);
  const [price, setPrice] = useState<string>('');
  const [openConfirm, setOpenConfirm] = useState(false);
  const [listPackageAmount, setListPackageAmount] = useState<any[]>([]);
  const [listPackageRate, setListPackageRate] = useState<any[]>([]);

  const [commission, setCommission] = useState<number>(0);
  const [roundId, setRoundId] = useState();
  const [claimLoading, setClaimLoading] = useState<boolean>(false);

  const { library, account, active } = useWeb3React();
  const [snackState, setSnackState] = useState<{ type: any; content: string }>({
    type: 'error',
    content: '',
  });



  const [roundInfo, setRoundInfo] = useState<TimeLineProp>({
    ratePerUSDT: 0,
    soldAmount: 0,
    title: 'Current round',
  });

  const getProvider = useCallback(() => {
    return library?.provider || window.web3?.currentProvider;
  }, [library?.provider]);

  useEffect(() => {
    if (!account || !library) return;

    const web3 = new Web3(getProvider());
    const contractEcr20 = new web3.eth.Contract( erc20abi as any, getWalletAddressEcr20(), );

    contractEcr20.methods
      .balanceOf(account)
      .call()
      .then((res: any) => {
        setBalance(res || '0');
      });

  }, [library, account, getProvider]);



  const scrollToView = useCallback(() => {
    const timeLine = document.getElementById(TIMELINE_ID);
    timeLine?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  const getWalletAddress = () => {
    return process.env.REACT_APP_BSC_CONTRACT_ADDRESS;
  };

  const getWalletAddressEcr20 = () => {
    return process.env.REACT_APP_BSC_USDT_ADDRESS;
  };

  const checkApproveTx = useCallback(
    async (from?: string, spender?: string) => {
      const web3 = new Web3(getProvider());
      const lotteryEcr20 = new web3.eth.Contract(
        erc20abi as any,
        getWalletAddressEcr20(),
      );
      const allowance = await lotteryEcr20.methods
        .allowance(from, spender)
        .call();
      return allowance;
    },
    [getProvider],
  );

  const onBuySomeFi = async (_amount:string) => {
    const someFiAddress = getWalletAddress();
    const web3 = new Web3(getProvider());
    const lottery = new web3.eth.Contract(abi as any, someFiAddress);
    const lotteryEcr20 = new web3.eth.Contract(
      erc20abi as any,
      getWalletAddressEcr20(),
    );

    try {
      const amountValue = web3.utils.toBN(
        web3.utils.toWei(_amount + '', 'ether'),
      );
      const maxINT =
        '115792089237316195423570985008687907853269984665640564039457584007913129639935';
      const referrer = localStorage.getItem(LocalStorageEnum.REFERRAL);

      const isLeft = referrer
        ? window.atob(referrer).substring(window.atob(referrer).length - 1) ===
          'l'
        : false;
      const refDecoded = referrer && window.atob(referrer).slice(0, -1);

      if (!isApprove) {
        setProgressApprove(true);
        const result = await lotteryEcr20.methods
          .approve(someFiAddress, maxINT)
          .send({
            from: account,
          });
        setProgressApprove(false);
        setIsApprove(!!result);
        return;
      }

      setOpenLoading(true);

      await lottery.methods
        .buyICOByUSDT(refDecoded || NONE_ADDRESS, isLeft, amountValue)
        .send({
          from: account,
        });
      setOpenLoading(false);
      setSnackState({
        type: 'success',
        content: 'Your order was successful',
      });
      setOpenSnack(true);

    } catch (e) {
      setOpenLoading(false);
      setProgressApprove(false);
      setSnackState({
        type: 'error',
        content: 'Transaction failed',
      });
      setOpenSnack(true);
      console.log(e);
    }
  };

  const onAddSomeFiToMetamask = async () => {
    try {
      await library?.provider.request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20',
          options: {
            address: getWalletAddress(), // The address that the token is at.
            symbol: 'SomeFi', // A ticker symbol or shorthand, up to 5 chars.
            decimals: 18, // The number of decimals in the token
          },
        },
      });
    } catch (error) {
      console.log(error);
    }
  };

  const est = useMemo(() => {
    return Math.floor(Number(amount || 0) * Number(price || 1));
  }, [amount, price]);

  const renderTextBtn = useMemo(() => {
    if (isExpired) return 'Buy SOFI has expired';
    if (isApprove) return 'Buy SOFI';
    return 'Enable USDT';
  }, [isApprove, isExpired]);

  useEffect(() => {
    if (!active) return;

    setProgressApprove(true);
    setIsApprove(false);
    checkApproveTx(account || '', getWalletAddress())
      .then((res: any) => {
        setIsApprove(!!(res > 0));
      })
      .finally(() => {
        setProgressApprove(false);
      });
  }, [active, account, checkApproveTx]);

  /**
   * set round is ended ?
   */
  useEffect(() => {
    const provider = getProvider();
    if (!active || !provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(abi as any, getWalletAddress());

    contract.methods
      .icoHasEnded()
      .call()
      .then((res: boolean) => {
        setIsExpired(res);
      });
  }, [active, getProvider]);

  /**
   * set price (get from contract)
   */
  useEffect(() => {
    const provider = getProvider();
    if (!active || !provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(abi as any, getWalletAddress());

    contract.methods
      .ratePerUSDT()
      .call()
      .then((res: any) => {
        setPrice(res);
      })
      .catch(() => {});
  }, [active, getProvider]);

  const disabled = () => {
    const balanceCoin = parseFloat(formatEther(balance));
    if (balanceCoin < Number(amount || 0)) return true;
    return !amount || !account || isExpired || isProgressApprove || openLoading;
  };

  /**
   * disable claim
   */
  const disableClaim = useMemo(() => {
    if (Number(commission) > 0) return false;
    return true;
  }, [commission]);

  /**
   * handle claim commission
   */


  /**
   * set commisson of ref
   * set node info (ref)
   */


  /**
   * set round information
   */
  const roundInfoFecth = useCallback(async () => {
    const provider = getProvider();
    if (!provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(abi as any, getWalletAddress());
    try {
      const roundId = await contract.methods.roundId().call();
      const res = await Promise.all([
        contract.methods.totalAmount().call(),
        contract.methods.ratePerUSDT().call(),
        contract.methods.getSoldbyRound(roundId).call(),
      ]);

      const [totalAmount, ratePerUSDT, soldAmount] = res;
      setRoundInfo({
        ratePerUSDT: Number((1 / ratePerUSDT).toFixed(6)),
        soldAmount: Number(soldAmount) / 10 ** 18,
        title: 'Current round',
      });
    } catch (error) {}
  }, [getProvider]);

  useEffect(() => {
    roundInfoFecth();
  }, [roundInfoFecth]);

  const packageInfoFetch = useCallback(async () => {
    const provider = getProvider();
    if (!provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(abi as any, getWalletAddress());

    try {
      for (let i = 0; i < 5; i++) {
        let pkgAmount = await contract.methods.pkgAmount(i).call();
        let pkgRate = await contract.methods.pkgRate(i).call();

        setListPackageAmount(listPackageAmount => [...listPackageAmount, pkgAmount]);
        setListPackageRate( listPackageRate => [...listPackageRate ,pkgRate ]);
      }
    } catch (error) {}
  }, []);

  useEffect(() => {
    packageInfoFetch();
  }, [packageInfoFetch]);

  return (
    <Box className={classes.container}>
      <ConnectWallet
        setSnackState={setSnackState}
        setOpenSnack={setOpenSnack}
      />
      <Grid className={classes.title}>
        <span>User Info</span>
      </Grid>
      <NodeInfo></NodeInfo>
      <Grid className={classes.title}>
        <span>Package</span>
      </Grid>
      <Package></Package>
      <CommonSnackBar
        openSnack={openSnack}
        setOpenSnack={setOpenSnack}
        type={snackState.type}
        content={snackState.content}
      />
      <ConfirmReferral
        openConfirm={openConfirm}
        setOpenConfirm={setOpenConfirm}
        setSnackState={setSnackState}
        setOpenSnack={setOpenSnack}
      />
    </Box>
  );
};

export default BuyToken;

