import React, { useState, useCallback, useEffect } from 'react';
import { Typography } from '@material-ui/core';
import { useStyles } from './styles';
import Web3 from 'web3';
import isNil from 'lodash/isNil';
import { abi } from 'services/contract';
import { WEI_PER_USDT } from 'common/constant';
import { numberWithCommas } from 'helpers/index';

export interface ReportProps {
  getProvider: () => any;
  getSomeFiContractAddress: () => string | undefined;
  account?: string | null;
}

const Report = (props: ReportProps) => {
  const { getProvider, getSomeFiContractAddress } = props;
  const [soldUSDT, setSoldUSDT] = useState<string | undefined>(undefined);
  const [rate, setRate] = useState<number>(0);
  const [roundId, setRoundId] = useState<number>(1);
  const styles = useStyles();

  const getSoldToken = useCallback(async () => {
    const someFiContractAddress = getSomeFiContractAddress();
    const web3 = new Web3(getProvider());
    const someFiContract = new web3.eth.Contract(
      abi as any,
      someFiContractAddress,
    );

    try {
      const _roundId = await someFiContract.methods.roundId().call();
      // const _roundStartTime = await someFiContract.methods.startTimeICO().call();
      const _rate = await someFiContract.methods.ratePerUSDT().call();
      const soldWei = await someFiContract.methods
        .getSoldbyRound(_roundId)
        .call();
      const usdt = soldWei / WEI_PER_USDT;
      setSoldUSDT(numberWithCommas(usdt));
      setRate(usdt / _rate);
      setRoundId(_roundId);
    } catch (error) {
      console.log('error: ', error);
    }
  }, [setSoldUSDT, getProvider, getSomeFiContractAddress]);

  useEffect(() => {
    getSoldToken();
  }, [getSoldToken]);

  return (
    <div className={styles.container}>
      <div className={styles.labelContainer}>
        <div>
          <div className={styles.label}>Current Round</div>
          <Typography>
            Round ID: <span>{roundId}</span>
          </Typography>
        </div>
        <div>
          <Typography>
            Total Sold Token (SOFI):{' '}
            <span>{`${isNil(soldUSDT) ? '- - - -' : soldUSDT}`}</span>
          </Typography>
        </div>
        <div>
          <Typography>
            Total USDT received:{' '}
            <span>{`${isNil(rate) ? '- - - -' : rate}`}</span>
          </Typography>
        </div>
      </div>
    </div>
  );
};

export default Report;
