import React from 'react';
import { makeStyles } from '@material-ui/core';
import { withStyles, Theme, createStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

interface TableListProp {
  columns: string[];
  rows: any[];
  className?: string;
  profile?: boolean;
}

function TableList(props: TableListProp) {
  const classes = useStyles();
  const { rows, columns, className, profile } = props;
  const StyledTableCell = withStyles((theme: Theme) =>
    createStyles({
      head: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
      },
      body: {
        fontSize: 14,
        color: 'black',
      },
    }),
  )(TableCell);

  const StyledTableRow = withStyles((theme: Theme) =>
    createStyles({
      root: {
        '&:nth-of-type(odd)': {
          backgroundColor: theme.palette.action.hover,
        },
      },
    }),
  )(TableRow);

  return (
    <TableContainer component={Paper} className={className}>
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            {columns.map((col, index) => (
              <TableCell
                className={classes.tableCell}
                key={col}
                align={index === 0 ? 'left' : 'center'}
              >
                {col}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row, index) => (
            <StyledTableRow key={row.address}>
              {!profile ? (
                <>
                  <StyledTableCell component="th" scope="row">
                    {index + 1}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    {row?.address}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    {Number(row?.totalPackageSize) / 10 ** 18}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    {Number(row?.branchInvestment) / 10 ** 18}
                  </StyledTableCell>
                  <StyledTableCell align="center">
                    {Number(row?.profit) / 10 ** 18}
                  </StyledTableCell>
                </>
              ) : (
                <>
                  <StyledTableCell component="th" scope="row">
                    {row.left}
                  </StyledTableCell>{' '}
                  <StyledTableCell component="th" scope="row">
                    {row.right}
                  </StyledTableCell>
                </>
              )}
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default TableList;

const useStyles = makeStyles((theme) => ({
  table: {
    minWidth: 700,
    background: 'white',
  },
  tableCell: {
    background: 'linear-gradient(251deg, rgb(139, 51, 124), rgb(199, 43, 97))',
    color: theme.palette.common.white,
    fontSize: 20,
  },
}));
