import {
  Box,
  Button,
  Divider,
  makeStyles,
  Typography,
} from '@material-ui/core';
import { useWeb3React } from '@web3-react/core';
import clsx from 'clsx';
import React from 'react';
import ConnectWalletIcon from '../../icons/ConnectWalletIcon';
import MetamaskIcon from '../../icons/MetamaskIcon';
import { injected, walletconnect } from '../../services/wallet';

interface IProps {
  setOpen: (value: boolean) => void;
  setConnector: (value: string) => void;
}

const ConnectDialog = ({ setOpen, setConnector }: IProps) => {
  const classes = useStyles();

  const { activate } = useWeb3React();

  const loginWithMetamask = () => {
    setConnector('injected');
    activate(injected);
    setOpen(false);
  };

  const loginWithConnectWallet = () => {
    setConnector('walletconnect');
    activate(walletconnect);
    setOpen(false);
  };

  return (
    <Box className={classes.container}>
      <Box className={clsx(classes.header, 'center-root')}>
        <Typography className={classes.title}>Connect Wallet</Typography>
      </Box>
      <Divider orientation="horizontal" className={classes.divider} />
      <Box className={classes.main}>
        <Button
          className={classes.btn}
          disableRipple
          onClick={loginWithMetamask}
        >
          <Box className={classes.inner}>
            <MetamaskIcon />
            <Typography className={classes.label}>Metamask</Typography>
          </Box>
        </Button>
        <Button
          className={classes.btn}
          disableRipple
          onClick={loginWithConnectWallet}
        >
          <Box className={classes.inner}>
            <ConnectWalletIcon />
            <Typography className={classes.label}>WalletConnect</Typography>
          </Box>
        </Button>
      </Box>
    </Box>
  );
};

export default ConnectDialog;

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    backgroundColor: 'rgb(42, 41, 53)',
    border: '2px solid rgb(0, 0, 0)',
    borderRadius: 16,
    flexDirection: 'column',
    maxWidth: 600,
  },
  header: {
    padding: 16,
  },
  title: {
    fontSize: '1.3118rem',
    fontWeight: 600,
  },
  divider: {
    borderColor: 'rgb(68, 65, 101)',
    borderWidth: '0px 0px thin',
    borderStyle: 'solid',
    margin: 0,
  },
  main: {
    display: 'flex',
    justifyContent: 'space-around',
    padding: 16,
  },
  btn: {
    border: 0,
    outline: 0,
    margin: 0,
    padding: 0,
  },
  inner: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: 16,
    '& svg': {
      width: 40,
      height: 40,
    },
  },
  label: {
    marginTop: 16,
    fontSize: '1rem',
    whiteSpace: 'pre-line',
  },
}));
