export interface ReferralProp {
  canBeRef: boolean;
  isLeft: boolean;
  referralLink: string;
  member?: number | 0;
  investment?: number | 0;
}

function Referral(props: ReferralProp) {
  const {
    canBeRef,
    isLeft,
    referralLink,
    member,
    investment,
  } = props;

  const classes = useStyles();

  const handleCopy = (str: string | null | undefined) => {
    if (!isNil(str)) navigator.clipboard.writeText(str);
  };

  return (
      <div className={classes.refProgram}>
        <div>
          <p style={{fontWeight: 'bold', fontSize:"22px", margin: 0}}>{isLeft ? "TEAM LEFT" : "TEAM RIGHT"}</p>
          {!canBeRef ? (
              <div className={classes.text}>
                You have to buy an investment package to become a referral !
              </div>
          ) : (
              <>
                <div>
                  <Box display={'flex'} alignItems={'center'}>
                    <p>
                      {renderShortAddress(
                          referralLink,
                          24,
                          7,
                      )}
                    </p>
                    <Button
                        onClick={() => {
                          handleCopy(referralLink);
                        }}
                        className={classes.copy}
                    >
                      copy
                    </Button>
                  </Box>
                </div>
              </>
          )}
        </div>
        <div className={classes.branchInfo}>
          <div className={classes.branchNumber}>
            <p style={{fontWeight: 'bold', fontSize:"18px", padding: "10px 20px", color: "#d1d3df", margin: 0}}>Member</p>
            <p style={{fontWeight: 'bold', fontSize:"28px", margin: 0, padding: "10px 20px"}}>{member ? member : "0"}</p>
          </div>
          <div className={classes.branchInvest}>
            <p style={{fontWeight: 'bold', fontSize:"18px", margin: 0, color: "#d1d3df",  padding: "10px 20px"}}>Investment</p>
            <p style={{fontWeight: 'bold', fontSize:"28px", margin: 0, padding: "10px 20px"}}>{investment ? investment : "0"} </p>
          </div>
        </div>
      </div>
  );
}

export default Referral;

