import React, {
  PropsWithChildren,
  useCallback,
  useEffect,
  useState,
} from 'react';
import { Box, Button, Grid, makeStyles } from '@material-ui/core';
import { NONE_ADDRESS } from 'enums/const';
import { isNil } from 'lodash';
import { renderShortAddress } from 'helpers';
import UserInfo from './userInfo';
import theme from 'material';
import Web3 from 'web3';
import { abi } from 'services/contract';
import { useWeb3React } from '@web3-react/core';
import Referral from "./referral";
import axios from "axios";


export interface NodeInfoProp {
  canBeRef: boolean;
  parent: string;
  left: string;
  right: string;
}

function NodeInfo(props: PropsWithChildren) {
  const {
    children,
  } = props;

  const classes = useStyles();
  const { library, account } = useWeb3React();
  const [nodeInfo, setNodeInfo] = useState<NodeInfoProp>({
    parent: '',
    left: '',
    right: '',
    canBeRef: false,
  });

  const [totalMemberLeft, setTotalMemberLeft] = useState(0);
  const [totalMemberRight, setTotalMemberRight] = useState(0);
  const [totalInvestRight, setTotalInvestRight] = useState(0);
  const [totalInvestLeft, setTotalInvestLeft] = useState(0);
  const getProvider = useCallback(() => {
    return library?.provider || window.web3?.currentProvider;
  }, [library?.provider]);

  const getWalletAddress = () => {
    return process.env.REACT_APP_BSC_CONTRACT_ADDRESS;
  };

  const commissionFetch = useCallback(async () => {
    const provider = getProvider();
    if (!provider) return;
    const web3 = new Web3(provider);
    const contract = new web3.eth.Contract(abi as any, getWalletAddress());
    try {
      const roundId = await contract.methods.roundId().call();

      const refInfo = await contract.methods.refInfo(account, roundId).call();
      const {
        left,
        right,
        parent,
        canBeRef,
      } = refInfo;

      setNodeInfo({
        canBeRef,
        parent,
        left,
        right,
      });
    } catch (error) {}
  }, [account, getProvider]);

  useEffect(() => {
    commissionFetch();
  }, [commissionFetch]);

  useEffect( () => {
    async function getAccountInfo() {
      console.log("account", account)

      axios.get('http://0.0.0.0:8080/worker/v1/profile/' + account)
          .then(res => {
            const persons = res.data;
            console.log(persons)

            var {
              TotalmemberLeft,
              TotalmemberRight,
              BrachLeft,
              BrachRight
            } = persons

            if (TotalmemberLeft != 0) setTotalMemberLeft(TotalmemberLeft)
            if (BrachLeft != 0) setTotalInvestLeft(BrachLeft)
            if (TotalmemberRight != 0) setTotalMemberRight(TotalmemberRight)
            if (BrachRight != 0) setTotalInvestRight(BrachRight)
          })
          .catch(error => console.log(error));
    }

    getAccountInfo()
  }, [account])
  return (
      <Grid className={classes.container} container spacing={2}>
        <UserInfo totalPackageSize={0} amount={0} currentPackageSize={0} profitClaimed={0}></UserInfo>
        <Referral
            member={totalMemberLeft}
            isLeft={true}
            referralLink={nodeInfo.left}
            canBeRef={nodeInfo.canBeRef}
            investment={totalInvestLeft} />
        <Referral
            member={totalMemberRight}
            isLeft={false}
            referralLink={nodeInfo.right}
            canBeRef={nodeInfo.canBeRef}
            investment={totalInvestRight} />
      </Grid>

  );
}

export default NodeInfo;

const useStyles = makeStyles(() => ({
  container: {
    display: 'flex',
    flexDirection: 'row',
    borderRadius: 16,
    justifyContent: 'space-between',
    columnGap: "10px",
    width: "100%",
  },
  gridItem: {
    width: '100%',
  },
  item: {
    marginBottom: 7,
  },
  span: {
    fontWeight: 700,
  },
  copy: {
    color: 'white',
    display: 'flex',
    background: 'linear-gradient(251deg, rgb(139, 51, 124), rgb(199, 43, 97))',
    alignItems: 'center',
    borderRadius: 10,
    fontWeight: 600,
    padding: 0,
    fontSize: 10,
    height: 22,
    minWidth: 50,
    marginLeft: 8,
  },
  text: {
    textAlign: 'center',
    color: 'red',
    height: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
}));
