import React, { useCallback, useEffect, useState } from 'react';
import { Box, Button, makeStyles, Typography } from '@material-ui/core';
import { useWeb3React } from '@web3-react/core';
import { LocalStorageEnum } from 'enums/storage';
import { renderShortAddress } from 'helpers';
import {
  injected,
  networks,
  supportedChainIds,
  walletconnect,
} from 'services/wallet';
import localStorage from 'utils/localStorage';
import CommonDialog from '../common/CommonDialog';
import ConnectDialog from './ConnectDialog';

declare global {
  interface Window {
    web3: any;
    ethereum: any;
  }
}

interface IProps {
  setSnackState: (value: any) => void;
  setOpenSnack: (value: boolean) => void;
}

const ConnectWallet = ({ setSnackState, setOpenSnack }: IProps) => {
  const [open, setOpen] = useState(false);
  const [connector, setConnector] = useState('');

  const { deactivate, account, error, active, activate } = useWeb3React();

  const classes = useStyles();

  const onOpenDialog = useCallback(() => {
    if (!active) {
      setOpen(true);
      return;
    }
    deactivate();
    localStorage.removeItem(LocalStorageEnum.CONNECTOR);
    localStorage.removeItem(LocalStorageEnum.REFERRAL);
  }, [active, deactivate]);

  useEffect(() => {
    const connector = localStorage.getItem(LocalStorageEnum.CONNECTOR);
    if (connector) {
      setConnector(connector);
      if (connector === 'injected') activate(injected);
      else if (connector === 'walletconnect') activate(walletconnect);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const switchNetwork = async () => {
    try {
      await window.ethereum.request({
        method: 'wallet_addEthereumChain',
        params: [
          {
            ...(networks as any)[supportedChainIds[0]],
          },
        ],
      });
    } catch (e) {
      setSnackState({
        type: 'error',
        content: 'Some errors happened',
      });
    }
  };

  useEffect(() => {
    if (!active) return;
    localStorage.setItem(LocalStorageEnum.CONNECTOR, connector);
  }, [active, connector]);

  useEffect(() => {
    if (!error) return;
    if (
      error.name === 'UnsupportedChainIdError' ||
      error.message.includes('Unsupported chain id')
    ) {
      switchNetwork();
      return;
    }
    setSnackState({
      type: 'error',
      content: 'Please authorize to access your account',
    });
    setOpenSnack(true);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [error]);

  return (
    <Box className={classes.container}>
      {/* <Typography className={classes.name}>Buy SOFI Token</Typography> */}
      <Button className={classes.btn} onClick={onOpenDialog}>
        {active ? renderShortAddress(account || '', 2, 4) : 'Connect Wallet'}
      </Button>
      <CommonDialog open={open} setOpen={setOpen}>
        <ConnectDialog setOpen={setOpen} setConnector={setConnector} />
      </CommonDialog>
    </Box>
  );
};

export default ConnectWallet;

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    padding: '15px 20px',
    justifyContent: 'flex-end',
    [theme.breakpoints.down('md')]: {
      padding: '0 15px',
    },
  },
  name: {
    fontSize: 20,
    fontWeight: 600,
    whiteSpace: 'pre-line',
    marginRight: 15,
    [theme.breakpoints.down('sm')]: {
      marginRight: 5,
    },
  },
  btn: {
    marginLeft: 8,
    textTransform: 'none',
    padding: '6px 16px',
    fontSize: 14,
    borderRadius: 10,
    height: 36,
    whiteSpace: 'nowrap',
    background:
      'linear-gradient(251deg, rgb(139, 51, 124), rgb(199, 43, 97)) !important',
    fontWeight: 600,
  },
}));
